import React from 'react'
import Link from 'gatsby-link'
import get from 'lodash/get'
import Helmet from 'react-helmet'


// import Lightbox from 'react-images'
import Gallery from "../components/Gallery"

import thumb01 from '../assets/images/thumbs_ML/tazab.jpg'
import thumb02 from '../assets/images/thumbs_ML/taza 2.jpg'
import thumb03 from '../assets/images/thumbs_ML/taza 3.jpg'
import thumb04 from '../assets/images/thumbs_ML/taza 4.jpg'

import full01 from '../assets/images/fulls/taza.jpg'
import full02 from '../assets/images/fulls/taza 2.jpg'
import full03 from '../assets/images/fulls/taza 3.jpg'
import full04 from '../assets/images/fulls/taza 4.jpg'

const DEFAULT_IMAGES = [
    { id: '1', src: full01, thumbnail: thumb01, caption: 'Photo 1', description: 'Set De Jardín De Infantes Personalizado En Quilmes Cole Kit'},
    { id: '2', src: full02, thumbnail: thumb02, caption: 'Photo 2', description: 'Lorem ipsum dolor sit amet nisl sed nullam feugiat.'},
    { id: '3', src: full03, thumbnail: thumb03, caption: 'Photo 3', description: 'Lorem ipsum dolor sit amet nisl sed nullam feugiat.'},
    { id: '4', src: full04, thumbnail: thumb04, caption: 'Photo 4', description: 'Lorem ipsum dolor sit amet nisl sed nullam feugiat.'}
];

class HomeIndex extends React.Component {

    

    render() {
        //const siteTitle = this.props.data.site.siteMetadata.title
        //const siteDescription = this.props.data.site.siteMetadata.description

        return (
            <div>
                <Helmet>
                        <title>hi</title>
                        <meta name="description" />
                </Helmet>

                <div id="main">

                    <section id="one">
                        <header>
                            <h2>Taza Portafoto De Plastico Personalizada</h2>
                            <h4>$60</h4> 
                        </header>
                        <p>DESCRIPCIÓN:<br />
                        * TAZAS PORTAFOTO PERSONALIZADAS<br />
                        * La imagen de la taza se estampa sobre tela, no sobre papel, se puede mojar o lavar.<br />
                        * Con cierre a presión.<br />
                        Colores disponibles: azul, rojo y blanco.<br />
                        Ideales para el cole, cumpleaños infantiles/adultos, egresados, baby shower, etc.<br />
                        Personalizadas a elección con imagen y/o foto, nombre.</p>
                         
                       <a mp-mode="dftl" href="https://www.mercadopago.com/mla/checkout/start?pref_id=8068205-259b339f-fcb2-4dfa-8d7e-d7bf454d2bcb" name="MP-payButton" className='blue-ar-l-rn-none preview__button'>Pagar</a><br /><br /> 

                        <Link to="/">Volver</Link>
                        <br /><br />  

                    </section>
                
                        <h2></h2>
                    
                        <Gallery images={DEFAULT_IMAGES.map(({ id, src, thumbnail, caption, description }) => ({
                            src,
                            thumbnail
                        }))} />

                        
                    

                </div>

            </div>
        )
    }
}

export default HomeIndex


